.header{
  padding: 15px 20px;
  background: #24292e;
  border-bottom: 1px solid #dddddd;
  &__wrap{
    display: flex;
    justify-content: space-between;
  }
  a{
    color: #fff;
    text-decoration: none;
  }
  &__title{
    text-align: center;
  }
}
.container {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
}